<template>
  <div class="body">
    <!-- 使用 TopNav 组件 -->
    <TopNav />

    <div class="page">
      <!-- 选择栏 -->
      <div class="selection-bar">
        <div
          v-for="project in projects"
          :key="project.name"
          :class="['selection-item', { active: selectedProject === project.name }]"
          @click="selectProject(project.name)"
        >
          {{ project.name }}
        </div>
      </div>

      <div class="mode">
        <div class="mode1">
          <div class="modetitle">
            <div class="gang"></div>
            <span>代表工程</span>
            <div class="gang"></div>
          </div>
          <div class="modepage">
            <div class="contact" @click="$router.push(selectedProjectData.path)">
              <div class="conimg">
                <img :src="selectedProjectData.image" alt="">
              </div>
              <div class="conitem">{{ selectedProjectData.name }}</div>
            </div>
          </div>
        </div>
      </div>
      <div>
        <FootIndex></FootIndex>
      </div>
    </div>
  </div>
</template>

<script>
import TopNav from '@/components/TopNav.vue' // 引入 TopNav 组件
import FootIndex from '@/components/FootIndex.vue'

export default {
  name: 'EngineeringIndex',
  components: {
    TopNav, // 注册 TopNav 组件
    FootIndex
  },
  data () {
    return {
      selectedProject: '咸九高速', // 默认选中的工程
      projects: [
        { name: '咸九高速', image: require('@/assets/代表工程/咸九高速.jpeg'), path: '/project/xianjiu' },
        { name: '松枝长江大桥', image: require('@/assets/代表工程/松枝长江大桥.jpeg'), path: '/project/songzhi' },
        { name: '武汉天河机场', image: require('@/assets/代表工程/武汉天河机场.jpeg'), path: '/project/tianhe' },
        { name: '深圳地铁13号线', image: require('@/assets/代表工程/深圳地铁13号线.jpeg'), path: '/project/shenzhen' },
        { name: '燕矶长江大桥', image: require('@/assets/代表工程/燕矶长江大桥.jpeg'), path: '/project/yanji' },
        { name: '鄂州花湖机场', image: require('@/assets/代表工程/鄂州花湖机场.jpeg'), path: '/project/ezhou' }
      ]
    }
  },
  computed: {
    // 根据选中的工程名称获取对应的工程数据
    selectedProjectData () {
      return this.projects.find(project => project.name === this.selectedProject)
    }
  },
  methods: {
    // 选择工程
    selectProject (projectName) {
      this.selectedProject = projectName
    }
  }
}
</script>

<style lang="less" scoped>
.body {
  display: grid;
  grid-template-rows: 66px auto; /* 第一行是 nav 的高度，第二行是剩余空间 */
  height: 100vh;
}

.page {
  padding-top: 100px;
}

.selection-bar {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.selection-item {
  padding: 10px 20px;
  margin: 0 10px;
  cursor: pointer;
  font-size: 16px;
  font-family: SimSun;
  font-weight: bold;
  color: #000;
  border-bottom: 2px solid transparent;
}

.selection-item.active {
  border-bottom: 2px solid #004da0;
  font-weight: bold;
}

.modetitle {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px;
}

.modetitle span {
  font-size: 30px;
    padding-top: 0px;
    font-weight: bold;
    font-family: SimSun;
  color: #000;
  margin: 0 6px 10px 6px;
}

.gang {
  display: block;
  background: rgb(198, 198, 198);
  width: 1rem;
  height: 0.062rem;
  border-radius: inherit;
}

.modepage {
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 0.002rem solid rgb(220, 220, 220);
}

.contact {
  margin-top: 20px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.conimg {
  width: 60%;
  height: 500px;
  overflow: hidden;
}

.conimg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.conitem {
  width: 100%;
  height: 78px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  background-color: rgb(237, 235, 235);
}
</style>
